}

.containerBox_footer {
    color: yellowgreen;
    background: rgb(229, 229, 241);
    /* background: linear-gradient(322deg, rgba(9,9,121,0.5411298308385855) 50%, rgba(0,212,255,0.5103175059086135) 100%);  */
    display: block;
    width: 100%;
    max-width: 260;
    float: inline-end;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4);
    margin-top: 2em;
}
.head_footer {
    text-align: left;
}

.footerLinkContainer {
    /* background-color: aqua; */

}

.footerLinks {
    display: flex;
    justify-content: space-evenly;
}

.footerLinks > * {
    text-decoration: none;
}


.copyright_footer {
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
}

.division {
  display: flex;
  background-color: #28334aff;
}

.info {
  margin-bottom: 1%;
  margin-top: 1%;
  margin-left: 1%;
  background-color: #f65058ff;
  text-align: center;
  width: 35%;
  padding: 1%;
}

.materials {
  margin-bottom: 1%;
  margin-top: 1%;
  margin-right: 1%;
  padding: 1%;
  width: 100%;
  background-color: #fbde44ff;
}

.h1_findmyself {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Monoton", Helvetica, sans-serif;
    background: #3210c9;
    letter-spacing: 5px;
    display: inline;
    text-transform: uppercase;
    color: #0c9fda;
}

.message_findmyself>h1 {
    padding-top: 3%;
}
.message_findmyself>p {
    text-align: justify;
    padding: 3%;
}

.message_findmyself>img {
    width: 90%;
}

 @supports (-webkit-background-clip: text) {
    .h1_findmyself {
         color: transparent;
         background: linear-gradient(7deg, #0c9fda 50%, #3210c9 0);
         -webkit-background-clip: text;
     }
}


